import interfaces from '../../utils/interfaces.js';
import app from "../../App.vue";
export default {
	data() {
		return {
			good: [],
			id:0,
		}
	},
	onLoad(options) {
		this.id=options.id?options.id:0;
		this.loadData();
	},
	onShow() {
		if (!app.globalData.user_info) {
			wx.reLaunch({
				url: '/pages/auth/index',
			})
		}
	},
	methods: {
		loadData() {
			this.request({
				url: interfaces.goodDetail,
				data:{
					id:this.id
				},
				method:'post',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						this.good=res.data;
					}
				}
			})
		},
		goBack(){
			wx.navigateBack({
				delta: 1
			})
		},
		selectType(type){
			wx.navigateTo({
				url:"add?type="+type+"&good_id="+this.good.id
			})
		}
	}
}
